// types.ts
export interface Ingredient {
  name: string;
  price: number;
  default: boolean;
}

export interface Product {
  id: number;
  name: string;
  description: string;
  basePrice: number;
  image: string;
  calories: number;
  preparationTime: string;
  ingredients: Ingredient[];
}

// MenuCard.tsx
import React from 'react';
import { Product } from './types';

const MenuCard: React.FC<{ product: Product; onClick: () => void }> = ({ product, onClick }) => {
  return (
    <div className="menu-card" onClick={onClick}>
      <img src={product.image} alt={product.name} className="menu-card-image" />
      <h3>{product.name}</h3>
      <p>{product.description}</p>
      <p>${product.basePrice.toFixed(2)}</p>
    </div>
  );
};

export default MenuCard;

// Checkbox.tsx
import * as React from 'react';
import * as CheckboxPrimitive from '@radix-ui/react-checkbox';

const Checkbox = ({ checked, onCheckedChange }: { checked: boolean; onCheckedChange: () => void }) => {
  return <CheckboxPrimitive.Root checked={checked} onCheckedChange={onCheckedChange} className="checkbox" />;
};

export default Checkbox;

// Restaurant.tsx
import React, { useState } from 'react';
import Navbar from './Navbar';
import Sidebar from './Sidebar';
import Category from './Category';
import Order from './Order';
import MenuCard from './MenuCard';
import Checkbox from './Checkbox';
import { Product } from './types';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';

const products: Product[] = [
  {
    id: 1,
    name: 'Burger',
    description: 'Delicious beef burger',
    basePrice: 5.99,
    image: '/burger.jpg',
    calories: 550,
    preparationTime: '10 min',
    ingredients: [
      { name: 'Lettuce', price: 0.5, default: true },
      { name: 'Tomato', price: 0.5, default: true },
      { name: 'Cheese', price: 1.0, default: false },
    ],
  },
];

const Restaurant: React.FC = () => {
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [selectedIngredients, setSelectedIngredients] = useState<Set<string>>(new Set());

  const handleProductSelect = (product: Product) => {
    setSelectedProduct(product);
    setSelectedIngredients(new Set(product.ingredients.filter(i => i.default).map(i => i.name)));
  };

  return (
    <div>
      <Navbar />
      <Sidebar />
      <Category />
      <div className="menu-grid">
        {products.map(product => (
          <MenuCard key={product.id} product={product} onClick={() => handleProductSelect(product)} />
        ))}
      </div>
      <Order />
      <Dialog open={!!selectedProduct} onOpenChange={() => setSelectedProduct(null)}>
        {selectedProduct && (
          <DialogContent>
            <DialogHeader>
              <DialogTitle>{selectedProduct.name}</DialogTitle>
            </DialogHeader>
            <p>{selectedProduct.description}</p>
            <p>${selectedProduct.basePrice.toFixed(2)}</p>
          </DialogContent>
        )}
      </Dialog>
    </div>
  );
};

export default Restaurant;
